import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../../@core/services/auth.service';
import { Router } from '@angular/router';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToastService } from '../../../services/toast.service';
// import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-login-page',
  templateUrl: './login-page.component.html',
  styleUrls: ['./login-page.component.scss']
})
export class LoginPageComponent implements OnInit {
  form:FormGroup;
  errors: any = null;
  invalidCredentials: 'Xãy ra lỗi' | 'Tài khoản hoặc mật khẩu không hợp lệ' | null = null;
  constructor(private authService: AuthService,
     private router: Router,
      private formBuider:FormBuilder,
      private toastr:ToastService) { }

  ngOnInit() {
    this.form=this.formBuider.group({
      username:['',[Validators.required, Validators.minLength(5)]],
      password:['',[Validators.required]]
    })
  }

  get userNameErrors() {
    return this.form.get('username').errors
  }

  get userNameErrorsString() {
    return JSON.stringify(this.userNameErrors)
  }

  onSubmit() {
    this.errors = {
      username: {...this.userNameErrors}
    }

    console.log(this.form)
    
    if (this.form.invalid) return

    const account:Account =this.form.value;
    this.authService.signIn(account).subscribe(
      ()=> {
        this.router.navigate(['/products'])
      },
      (err) => {
        if(err.status === 401) {
          this.invalidCredentials = 'Tài khoản hoặc mật khẩu không hợp lệ'
          
        } else {
          this.invalidCredentials = 'Xãy ra lỗi'
        }
      }
    )
  }
}
